.createNote {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 1rem;
  margin: 3rem auto;
  width: 1000px;
  border-radius: 10px;
  background: #5f90d11a;
  box-shadow: rgb(0 0 0 / 50%) 0px 0px 10px 0px
}

.inpBox {
  display: flex;
  justify-content: center;
  border: none;
  margin: 0.5rem;
  padding: 5px;
  max-width: fit-content;
  background: rgb(255 255 255);
  box-shadow: black 0px 1px 10px -5px;
  outline: none;
  border-radius: 15px;
}

.inpBox::placeholder {
  font-weight: 700;
  font-size: 1.5rem;
  color: rgb(145, 145, 145);
}


.currentDate {
  color: rgb(0, 0, 0);
  font-weight: 500;
  margin: 14px;
}

.title-inp {
  text-transform: uppercase;
  font-size: 1.5rem;
  color: rgb(82, 171, 240);
  font-family: "Texturina", serif;
  text-align: center;
}

.note-dec {
  font-size: 1.5rem;
  font-family: "Texturina", serif;
  min-height: 250px;
  min-width: -webkit-fill-available;
  resize: none;
  display: flex;
  justify-content: center;
  border: none;
  margin: 0.5rem;
  padding: 1rem;
  background: rgb(255 255 255);
  box-shadow: black 0px 1px 10px -5px;
  outline: none;
  border-radius: 15px;
}

.addBtn {
  color: white;
  padding: 10px;
  background-color: rgb(26, 87, 138);
  padding: 10px 35px;
  margin: 1rem auto;
  text-align: center;
  border-radius: 10px;
}

.addBtn:hover {
  background-color: rgb(122, 175, 219);
}

.sidenav a {
  position: absolute;
  left: 0px;
  margin-top: 10rem;
  padding: 15px 70px 46px 20px;
  width: 100px;
  height: 55px;
  text-decoration: none;
  font-size: 20px;
  color: rgb(0, 0, 0);
  border-radius: 0 5px 5px 0;
  box-shadow: black 2px 0px 5px -2px;
}

#italic {
  top: 20px;
  background-color: #ffffff;
  box-shadow: black 0px 0px 6p 0px;
}

#italic:hover {
  background-color: #a8a6a6;
}

#bold {
  top: 95px;
  background-color: #ffffff;
}

#bold:hover {
  background-color: rgb(168, 166, 166);
}

#color {
  top: 170px;
  background-color: #ffffff;
}

#contact {
  top: 245px;
  background-color: rgb(255, 255, 255)
}

.inputFont {
  width: 60px;
  font-size: 18px;
  padding-left: 5px;
}

.edit_menu_side {
  display: block;
}

.edit_menu_up {
  display: none;
}

/* ---------------------------------for 1230 screen size------------------------ */
@media (max-width:1230px) {
  .createNote {
    width: 680px;
  }

  #mySidenav a {
    padding: 15px 0px 40px 10px;
    width: 80px;
    font-size: 17px;
  }

  #italic {
    top: 20px;
    background-color: #ffffff;
    box-shadow: black 0px 0px 6p 0px;
  }

  #italic:hover {
    background-color: #a8a6a6;
  }

  #bold {
    top: 85px;
    background-color: #ffffff;
  }

  #bold:hover {
    background-color: rgb(168, 166, 166);
  }

  #color {
    top: 150px;
    background-color: #ffffff;
  }

  #contact {
    top: 215px;
    background-color: rgb(255, 255, 255)
  }
}


/* -------------------------for below 880 screen size--------------------- */
@media (max-width:980px) {
  .createNote {
    width: 570px;
  }

  .title-inp {
    font-size: 1rem;
  }

  .inpBox::placeholder {
    font-weight: 700;
    font-size: 1rem;
    color: rgb(145, 145, 145);
  }
}

/* ------------------------------------for below 780px screen devise---------------------- */
@media (max-width:780px) {
  .createNote {
    width: 350px;
  }

  .title-inp {
    font-size: 1rem;
    height: 50px;
    width: 170px;
  }

  .inpBox::placeholder {
    font-weight: 500;
    font-size: 1rem;
    color: rgb(145, 145, 145);
  }

  .currentDate {
    font-size: 14px;
    margin: 10px;
  }
}

/* ----------------------for below 550px screen device-------------------- */
@media (max-width:550px) {
  .edit_menu_side {
    display: none;
  }

  .edit_menu_up {
    display: block;
  }

  .edit_menu_italic,
  .edit_menu_bold,
  .edit_menu_color {
    background-color: white;
    padding: 7px 20px;
    box-shadow: 0px 0px 5px 1px #6eafff;
    border-radius: 5px;
  }

  .edit_inputFont {
    width: 31px;
    font-size: 18px;
    padding-left: 5px;
    border: none;
    border-bottom: 1px solid black;
    background-color: rgba(0, 0, 0, 0.089);
    outline: none;
  }
}


/* -----------------------------------for below 460px screen device--------------------- */
@media (max-width:460px) {
  .createNote {
    width: 300px;
  }

  .currentDate {
    font-size: 10px;
    margin: 10px
  }

  .edit_menu_side {
    display: none;
  }

  .edit_menu_up {
    display: block;
  }

  .edit_menu_italic,
  .edit_menu_bold,
  .edit_menu_color {
    background-color: white;
    padding: 5px 10px;
    font-size: 13px;
    box-shadow: 0px 0px 5px 1px #6eafff;
    border-radius: 5px
  }

  .edit_inputFont {
    width: 31px;
    font-size: 18px;
    padding-left: 5px;
    border: none;
    border-bottom: 1px solid black;
    background-color: rgba(0, 0, 0, 0.089);
    outline: none;
  }

  .editNote>.note-dec {
    font-family: "Texturina", serif;
    min-height: 250px;
    min-width: 327px;
    resize: none;
    display: flex;
    justify-content: center;
    border: none;
    margin: 0.5rem;
    padding: 1rem;
    background: rgb(255 255 255);
    outline: none;
    border-radius: 15px;
  }
}