.register{
    width: 550px;
    background-color: #fff;
    border: 1px solid #dddfe2;
    box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
    border-radius: 8px;
    padding:1rem;
    align-items: center;
    text-align: center;
  }
  
  .register input{
    border-radius: 8px;
    border: 1px solid black;
    outline: none;
    color: black;
    margin: 10px auto;
    padding: 15px 15px;
    width: 80%;
    font-size: 1rem;
  }
  .register input::placeholder{
    padding-left: 15px;
  }