.saveNote {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: auto;

}

.get-date {
  margin-bottom: 0;
  font-weight: 500;
  text-align: center;
  color: rgb(0, 0, 0);
}

.get-title {
  font-family: 'kanit', sans-serif;
  display: flex;
  align-items: center;
  font-weight: 600;
  color: #2396d8;
  text-transform: uppercase;
  font-size: 20px;
}

.inote-Card {
  display: flex;
  flex-direction: column;
  background: rgb(255 255 255);
  padding: 1rem;
  border-radius: 20px;
  width: 290px;
  min-height: 400px;
  margin: 1rem;
  position: relative;
  box-shadow: rgb(0 0 0 / 50%) 0px 0px 10px 0px;

}

.decp-box {
  font-size: 1.2rem;
  font-weight: 500;
  display: flex;
  width: 100%;
  resize: none;
  color: rgb(0, 0, 0);
  flex: 1;
  font-family: 'kanit', sans-serif;
  outline: none;
  border: none;
  background-color: rgb(255, 255, 255);
}

.dlt {
  cursor: pointer;
  width: 10%;
  font-size: 20px;
  display: inline-flex;
  align-items: center;
  color: #2479c3;
}

.iconHover:hover {
  box-shadow: 2px 1px 20px 2px;
}

.dlt:hover {
  box-shadow: 6px 3px 20px 7px;
}

.editNote {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 1rem;
  margin: 3rem auto;
  border-radius: 20px;
  background: rgb(236 240 255);
  box-shadow: rgb(0 0 0 / 50%) 0px 0px 20px;
}

.editNote>.inpBox {
  justify-content: center;
  border: none;
  margin: 0.5rem;
  width: 75%;
  background: rgb(76, 76, 77);
  outline: none;
  border-radius: 15px;
}

.editNote>.inpBox::placeholder {
  color: white;
}


.editNote>.currentDate {
  color: white;
  float: right;
  margin: 14px;
}

.editNote>.title-inp {
  font-size: 1.5rem;
  color: rgb(82, 171, 240);
  font-family: "Texturina", serif;
  text-align: center;
}

.editNote>.note-dec {
  font-family: "Texturina", serif;
  min-height: 250px;
  min-width: 400px;
  resize: none;
  display: flex;
  justify-content: center;
  border: none;
  margin: 0.5rem;
  padding: 1rem;
  background: rgb(255 255 255);
  outline: none;
  border-radius: 15px;
}

.saveEdit {
  color: white;
  padding: 10px;
  background-color: steelblue;
  padding: 10px 35px;
  margin: 1rem auto;
  text-align: center;
  border-radius: 10px;
}

.saveEdit:hover {
  background-color: rgb(122, 175, 219);
}


@media (max-width: 465px) {

  .editNote>.note-dec {
    font-family: "Texturina", serif;
    min-height: 250px;
    min-width: 300px;
    resize: none;
    display: flex;
    justify-content: center;
    border: none;
    margin: 0.5rem;
    padding: 1rem;
    background: rgb(255 255 255);
    outline: none;
    border-radius: 15px;

  }

}