.container {
  margin-top: 3rem;
  display: flex;
  justify-content: center;
}

.login {
  width: 400px;
  /* color: white; */
  background-color: rgb(255 255 255 / 48%);
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  border-radius: 8px;
  padding: 1rem;
  align-items: center;
  text-align: center;
}

.login input {
  border: none;
  border-bottom: 2px solid black;
  /* border-radius: 8px; */
  /* border: 1px solid black; */
  outline: none;
  color: black;
  margin: 10px auto;
  padding: 15px 15px;
  width: 80%;
  font-size: 1rem;
  background: rgb(74 85 163 / 4%);
}

.login input::placeholder {
  padding-left: 15px;
}

.login input[id=inp] {
  padding-left: 15px;
}

.btn {
  background: #1877f2;
  border-radius: 8px;
  font-size: 20px;
  padding: 10px;
  margin: 2rem auto;
  width: 82%;
  color: white;
}

@media (min-width:"830px") {
  .login input {
    margin: 8px auto;
    padding: 12px 12px;
    width: 70%;
    font-size: 0.8rem;
  }
}